
.App > .form {
  margin-bottom: 41px;
  margin-top: 41px;
}

.input-group {
  margin-bottom: 26px;
}
.input-group:nth-last-child(-n + 2) {
  margin-bottom: 50px;
}
.input-group > label,
.input-group > input {
  font-family: 'Roboto', sans-serif;
  max-width: 420px;
  text-align: left;
  width: 80%;
}
.input-group > label {
  color: #676767;
  display: inline-block;
  margin-bottom: 5px;
}
.input-group > input {
  background: #f2f2f5;
  border-radius: 4px;
  border: 1px solid #e5e5e5;
  color: #676767;
  font-size: 16px;
  height: 40px;
  outline: 0;
  padding: 0 15px;
  transition: all 0.3s;
}
.input-group > input:focus {
  background: #fff;
  border-color: #c4c4c4;
}
.input-group > input::placeholder {
  color: #c4c4c4;
  font-size: 16px;
}

.changepw {
  padding: 20px;
}

div > span {
  font-family: 'Open Sans', sans-serif;
  padding-top: 15px;
}

button.primary {
  background: #820f4c;
}
button.primary:hover {
  background: #460224;
}
button.secondary {
  background: #c1d7aa;
}
button.secondary:hover {
  background: #a8c787;
}
